@import url("https://fonts.googleapis.com/css2?family=Lexend+Deca&display=swap");
* {
  box-sizing: border-box;
}
body {
  font-family: "Lexend Deca", sans-serif;
}
.light {
  color: rgba(27, 38, 129, 1);
}
.text-style {
  border-style: solid;

  border-radius: 10px;

  margin-left: 25vw;
  margin-right: 15vw;

  padding-right: 4vw;

  height: auto;
  background: radial-gradient(
    119.18% 817.36% at 15.33% 45.29%,
    rgba(252, 240, 204, 0.7) 0%,
    rgba(196, 196, 196, 0.35) 100%
  );
  backdrop-filter: blur(3px);
}
.below {
  font-size: 2.5vw;

  margin-left: 23%;
  color: rgba(0, 0, 0, 1);
}
.word-style {
  font-size: 7vw;
  margin-top: 3%;
  margin-left: 18%;
  font-family: "Lexend Deca", sans-serif;
}
@media screen and (max-width: 450px) {
  .text-style {
    margin-top: 35%;
  }
}
@media screen and (max-width: 380px) {
  .text-style {
    margin-top: 15%;
  }
}
