.slider {
  position: absolute;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.image {
  width: 100vw;
  height: 100vh;
}
.right-arrow {
  position: absolute;
  top: 50%;
  margin-left: 95%;
  color: white;
  font-size: 200%;
  cursor: pointer;
  user-select: none;
}
.left-arrow {
  position: absolute;
  top: 50%;
  margin-right: 95%;
  color: white;
  font-size: 200%;
  cursor: pointer;
  user-select: none;
}
.slide {
  opacity: 0;
  transition-duration: 1s ease;
}
.slide.active {
  opacity: 1;
  transition-duration: 0.3s;
}
@media screen and (max-width: 450px) {
  .right-arrow {
    margin-left: 88%;
  }
}
